/*リンクの形状*/
#page-top a {
  align-items: center;
  background: #8e8eae;
  border-radius: 5px;
  color: #fff;
  display: flex;
  font-size: 0.6rem;
  height: 60px;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s;
  width: 60px;
}

#page-top a:hover {
  background: #8e8eae;
}

/*リンクを右下に固定*/
#page-top {
  bottom: 85px;
  /*はじめは非表示*/
  opacity: 0;
  position: fixed;
  right: 10px;
  transform: translateY(100px);
  z-index: 2;
}
@media screen and (min-width: 768px) {
  #page-top {
    bottom: 10px;
    right: 10px;
  }
}

/*　上に上がる動き　*/
#page-top.UpMove {
  animation: UpAnime 0.5s forwards;
}

@keyframes UpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/*　下に下がる動き　*/
#page-top.DownMove {
  animation: DownAnime 0.5s forwards;
}

@keyframes DownAnime {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(100px);
  }
}
